import React, {useState} from 'react';
import Dish from './Dish';
function DishContainer({onInput, recipes}) {
    const [query, setQuery] = useState('');
    const onChange = e =>{
        setQuery(e.target.value);
        
    }
    const onSubmit = e =>{
        e.preventDefault();
        console.log(query);
        onInput(query);
        setQuery('');
    }
    return (
        <div>
            <section className="dish newsletter" id="dish">
                <h1 className="heading"> <span> Your Food </span> </h1>
                <p>Search for fantastic</p>    
                <form onSubmit={onSubmit}>
                    <input type="text" placeholder="food lover ..." value={query} onChange={onChange} />
                    <input type="submit" value="Cook" />
                </form>
                <ul className="controls">
                    <li className="buttons button-active" data-filter="all">all</li>
                    <li className="buttons" data-filter="sandwich">sandwich</li>
                    <li className="buttons" data-filter="cake">cake</li>
                    <li className="buttons" data-filter="juice">juice</li>
                    <li className="buttons" data-filter="chocolate">chocolate</li>
                </ul>

                <div className="image-container">
                    {
                        recipes !== [] && recipes.map((recipe, index)=>{
                            return <Dish key={index} recipe={recipe} />
                        })
                    }
                </div>

            </section>
        </div>
    )
}

export default DishContainer
