import React from 'react'

function Opinion() {
    return (
        <section class="order" id="order">

            <h1 class="heading"> <span> your opinion </span> </h1>

            <form>
                <div class="inputBox">
                    <textarea cols="30" rows="10" placeholder="your opinion"></textarea>
                    <iframe title='map' id="map" src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3769.3448351768534!2d72.8194324144256!3d19.136377055045728!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3be7b7ddc82073d7%3A0x3aa5a7ce31b7b9dd!2sRestaurants!5e0!3m2!1sen!2sin!4v1612270030599!5m2!1sen!2sin"></iframe>
                </div>
                <input type="submit" value="Sent for us" class="btn" />
            </form>

        </section>
    )
}

export default Opinion;
