import Header from './component/Header';
import Home from './component/Home';
import About from './component/About';
import DishContainer from './component/Dish/DishContainer';
import Chef from './component/Chef';
import Suggest from './component/suggest';
import Opinion from './component/Opinion';
import Footer from './component/Footer';
import axios from 'axios';
import $ from 'jquery';
import React, {useState, useEffect} from 'react';
function App() {
  const [recipes, setRecipe] = useState([]);
  const APP_ID = "cdac11e6";
  const APP_KEY = "57151c988d289ebcf8c8339b8b75cc79";
  const onInput = async input =>{
    const result = await axios.get(`https://api.edamam.com/search?q=${input}&app_id=${APP_ID}&app_key=${APP_KEY}`);
    console.log(result.data.hits);
    setRecipe(result.data.hits);

  }
  useEffect(() => {
    $(document).ready(function(){

      $('#menu').click(function(){
          $(this).toggleClass('fa-times');
          $('.navbar').toggleClass('nav-toggle');
      });
  
      $(window).on('load scroll',function(){
  
          $('#menu').removeClass('fa-times');
          $('.navbar').removeClass('nav-toggle');
  
          if($(window).scrollTop() > 60){
              $('header .header-2').addClass('header-active');
          }else{
              $('header .header-2').removeClass('header-active');
          }
  
          $('section').each(function(){
  
              let height = $(this).height();
              let offset = $(this).offset().top - 200;
              let top = $(window).scrollTop();
              let id = $(this).attr('id');
  
              if(top >= offset && top < offset + height){
                  $('.navbar ul li a').removeClass('active');
                  $('.navbar').find(`[href="#${id}"]`).addClass('active');
              }
  
          });
  
      });
  
      $('.controls .buttons').click(function(){
  
          $(this).addClass('button-active').siblings().removeClass('button-active');
  
      });
  
  });
  }, [])
  return (
    <div className="App">
      <Header />
      <Home />
      <About />
      <DishContainer recipes={recipes} onInput={onInput} />
      <Chef />
      <Suggest />
      <Opinion />
      <Footer />
    </div>
  );
}

export default App;
