import React from 'react'
import video4 from '../video/four.mp4'
function Home() {
    return (
        <section className="home" id="home">
            <div className="video-container" >
                <video src={video4} loop autoPlay muted id="video-slider"></video>
            </div>
            <div className="content">
                <h1>welcome <span>food</span> lovers</h1>
                    <p>Lorem, ipsum dolor sit amet consectetur adipisicing elit. Distinctio eum explicabo voluptatum amet rerum dolorum placeat nisi dolore enim harum!</p>
                    <a href="https://www.latimes.com/food"><button className="btn">get started</button></a>
            </div>
            <div className="shape"></div>
        </section>
    )
}

export default Home
